.Lists {
  max-width: 894px;
  margin: 0 auto 110px;
  list-style-type: none;
  padding: 0 10px;
}

.Lists li {
  margin-bottom: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Lists li:last-of-type {
  margin-bottom: 0;
}

.todo_item {
  color: #8e8e8e;
  width: 78%;
  margin: 0;
  font-size: 1.4rem;
}

.delete {
  border: none;
  background: none;
  outline: none;
  width: 20px;
  padding: 0;
  opacity: 0.5;
}
.delete:active {
  opacity: 1;
}

.delete img {
  width: 16px;
}
