.form {
  max-width: 1004px;
  margin: 80px auto 40px;
  display: flex;
  justify-content: space-between;
}

.form input {
  font-weight: bold;
  font-size: 1.6rem;
  box-sizing: border-box;
  color: #8e8e8e;
  outline: none;
  border: none;
  width: 78%;
  height: 40px;
  padding: 10px 14px;
  border-radius: 3px;
  background: linear-gradient(145deg, #e2e6ec, #ffffff);
  box-shadow: 7px 7px 14px #d8dee6, -7px -7px 14px #ffffff;
}

.form button {
  box-sizing: border-box;
  outline: none;
  font-size: 2.4rem;
  border: none;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 3px;
  background: #f0f7ff;
  box-shadow: 7px 7px 14px #d8dee6, -7px -7px 14px #ffffff;
  text-align: center;
}
